<template>
  <div class="page page-deposit-transfer-in">
    <div class="scrollable">
      <img v-if="cryptoAccountInfo.address && isTrading" class="QRcode" alt="qr code" />
      <div class="notice">此地址只可接受USDT</div>
      <van-cell-group>
        <van-cell title="币种">
          <van-field value="USDT" readonly colon input-align="right" />
        </van-cell>
        <van-cell title="主网">
          <van-field
            value="Tronscan(TRC20)"
            readonly
            colon
            input-align="right"
          />
        </van-cell>
        <van-cell v-if="isTrading" title="转入地址">
          <van-field
            v-model="cryptoAccountInfo.address"
            readonly
            colon
            input-align="right"
          />
          <button
            v-clipboard="cryptoAccountInfo.address"
            class="copy copyBtn"
            @success="$root.copySuccessful"
            slot="right-icon"
          />
        </van-cell>
        <van-cell title="转入数量(USDT)">
          <van-field
            v-model="form.amount"
            :formatter="(v) => (isTrading ? parseFloat(v).toFixed(2) : v)"
            :readonly="isTrading"
            placeholder="请输入转入数量"
            colon
            input-align="right"
          />
        </van-cell>
      </van-cell-group>
      <CryptoDepositDescription />
    </div>
    <div class="actions">
      <van-button
        v-if="isTrading"
        class="primary block"
        :to="`/crypto_order_detail/${orderInfo.orderNo}`"
      >
        查看订单
      </van-button>
      <van-button v-else class="primary block" @click="businessDeposit">
        确定转入
      </van-button>
    </div>
  </div>
</template>

<script lang="ts">
import { businessDeposit } from "@/apis/crypto";
import CryptoDepositDescription from "@/components/CryptoDepositDescription.vue";
import { verifySecuretedCode } from "@/vendor/globalVerify";
import { cryptoMixins, cryptoTradingMixins } from "@/vendor/mixins";
import Vue from "vue";
export default Vue.extend({
  components: { CryptoDepositDescription },
  mixins: [cryptoMixins, cryptoTradingMixins],
  data() {
    return {
      orderType: "DepositTransferIn",
    };
  },
  created() {
    this.businessGetAccountInfo("USDT");
  },
  methods: {
    //  钱包转入
    async businessDeposit() {
      const securePassword = await verifySecuretedCode(this);
      const { ok, msg } = await businessDeposit({
        paymentType: "USDT",
        amount: parseFloat(this.form.amount).toFixed(2),
        passageType: "DepositTransferIn",
        receivingAddress: this.cryptoAccountInfo.address,
        securePassword,
      });
      if (ok) {
        this.$toast("订单创建成功");
        this.$router.go(0);
        // this.$router.back();
      } else {
        this.$toast(msg);
      }
    },
  },
});
</script>

